.popup {
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup__title {
  font-size: 30px;
}

.popup-inner {
  line-height: 1.4;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  max-width: 800px;
  text-align: center;
}

.subjectpopup-inner {
  line-height: 1.4;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  min-width: 600px;
  max-width: 800px;
  text-align: center;
}

.subjectpopup__table {
  border-collapse: collapse;
  border: 2px solid black;
  width: 100%;
}

.subjectpopup__table tr {
  border: 2px solid black;
}

.subjectpopup__table td {
  padding: 10px;
  justify-content: space-between;
  border: 2px solid black;
}

.key {
  font-weight: bolder;
}

.value {
  font-weight: normal;
}
