@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/variable/pretendardvariable-std-dynamic-subset.css");

* {
  margin: 0;
  font-family: 'Pretendard Variable';
}

.app {
  position: relative;
}

.app__header_container {
  position: relative;

  display: flex;
  flex-direction: row;
  height: 10vh;
  justify-content: flex-start;

  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.app__header {
  position: absolute;

  left: 3%;
  height: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.app__header_links {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.app_header_logo{
  margin-right: 20px;
  height: 60%;
  width: auto;
}

.links {
  box-sizing: border-box;
  text-decoration: none;
  color: black;
  font-size: 2.8vh;
  font-weight: 700;
  padding: 10px;
  margin-right: 15px;
  border: 2px solid white;
}

.links:hover {
  transition: 400ms;
  border-radius: 10px;
  background-color: #f0f0f0;
  border: 2px solid white;
}

.links:active {
  background-color: #e6e6e6;
  border: 2px solid white;
}

.link_current {
  transition: 400ms;
  box-sizing: border-box;
  text-decoration: none;
  color: black;
  font-size: 2.8vh;
  font-weight: 700;
  padding: 10px;
  margin-right: 15px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

/* .links:focus {
  border: 2px solid gray;
} */

.app__mainContainer {
  display: flex;
  flex-direction: row;

  margin-top: 3vh;
  height: 83vh;
}

.app__parentContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: stretch;
  align-content: stretch;

  width: 30%;
}

.app__headerText {
  margin: 20px;
  color: black;
}

.app__creationoptions {
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  padding: 20px;
  width: calc(100% - 40px);
  border-radius: 10px;
  margin-bottom: 5%;
  white-space: pre-wrap;
}

.app__creationoptions_contents {
  display: flex;
  flex-direction: column;
}



/* OTHER STYLES */

.mid_title {
  height: auto;
  flex: 0 0 auto;
  overflow: hidden;

  margin-top: -5px;
  margin-bottom: 10px;
  font-size: 25px;
  font-weight: 600;
}

.input-1 {
  text-align: left;
  font-weight: 300;
  font-size: 18px;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 6px;
  padding-bottom: 6px;
  box-sizing: border-box;
  flex-grow: 1;
}

.button-9 {
  margin-left: 20px;
  padding-left: 12px;
  padding-right: 12px;
  appearance: button;
  backface-visibility: hidden;
  background-color: #405cf5;
  border-radius: 6px;
  border-width: 0;
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset,rgba(50, 50, 93, .1) 0 2px 5px 0,rgba(0, 0, 0, .07) 0 1px 1px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-size: 100%;
  height: 40px;
  line-height: 1.15;
  outline: none;
  overflow: hidden;
  position: relative;
  text-align: center;
  text-transform: none;
  transform: translateZ(0);
  transition: all .2s,box-shadow .08s ease-in;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-9:disabled {
  cursor: default;
}

.button-9:focus {
  box-shadow: rgba(50, 50, 93, .1) 0 0 0 1px inset, rgba(50, 50, 93, .2) 0 6px 15px 0, rgba(0, 0, 0, .1) 0 2px 2px 0, rgba(50, 151, 211, .3) 0 0 0 4px;
}

.button-0 {
